.canopyDimensionMenuContainer {
    width: 32vw;
    margin: auto;
    background-color: teal;
}

.canopyDimensionButtonsContainer {
    background-color: #f5f5f3;
    display: flex;
    justify-content: space-around;
    padding: 2%;
}

.canopyDimensionButton {
    display: flex;
    width: 4vw;
    height: 5vh;
    position: relative;
    cursor: pointer;
    background-color: #ffffff;
}

.canopyDimensionButton:hover {
    background-color: #da8600;
    color: #ffffff;
}

.canopyDimensionButtonActive {
    display: flex;
    width: 4vw;
    height: 5vh;
    position: relative;
    cursor: pointer;
    background-color: #da8600;
    color: #ffffff;
}

#canopyDimensionButtonLabel {
    width: fit-content;
    margin: auto;
    font-family: sans-serif;
    font-size: 0.8rem;
}

.canopyDimensionImageContainer {
    box-shadow: inset 0px 7px 9px -7px #ddddd9;
    height: 50vh;
    background-color: #ebebe6;
    padding: 10px;
}

.canopyDimensionImage {
    width: 100%
}

.canopyFinishButtonsContainer {
    background-color: #f5f5f3;
    height: 13vh;
    display: flex;
    justify-content: space-around;
    padding-top: 2%;
    padding-left: 30%;
    padding-right: 30%;
}

.canopyFinishButton {
    display: flex;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
}

#canopyFinishButtonLabel {
    width: 3rem;
    margin: auto;
    font-family: sans-serif;
    font-size: 0.6rem;
    padding-top: 15%;
}
