.preconfigurationGalleryContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 95%;
    height: 70vh;
    margin: auto;
    padding: 5% 0% 5% 0%;
    background-color: lightcoral;
}

.ceilingHeightMenu {
    height: inherit;
    background-color: lawngreen;
}

#ceilingHeightImg {
    height: 80%
}

.preconfigurationMenu {
    height: inherit;
    background-color: lightblue;
}

.preconfigButtonsContainer {
    background-color: mediumslateblue;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.lampFinishMenu {
    height: inherit;
    background-color: olive;
}

.lampFinishButtonsContainer {
    background-color: palevioletred;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}