.stepContainer {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.2rem;
}

#stepName {
    margin-bottom: 0;
}

#stepDescription {
    margin-top: 0;
}


/* =========================== BOTTOM STEP BUTTONS ====================== */

.stepButtonContainer {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: start;
    margin: 10px;
  }
  
  .prevStep {
    outline: 0;
    width: 7vw;
    height: 6vh;
    border: 1px solid #7f7f7f;
    border-radius: 30px;
    background-color: transparent;
    cursor: pointer;
  }
  
  .nextStep {
    outline: 0;
    width: 7vw;
    height: 6vh;
    border: none;
    border-radius: 30px;
    background-color: #000000;
    color: #fbf4eb;
    cursor: pointer;
  }

  .nextStep:hover {
    outline: 0;
    width: 7vw;
    height: 6vh;
    border: none;
    border-radius: 30px;
    background-color: #da8600;
    color: #fbf4eb;
    cursor: pointer;
  }