.stepBar {
    height: 6vh;
    display: flex;
    flex-direction: row;
    font-size: 0.8rem;
    background-color: rgb(237 237 233);
    box-shadow: inset 0px 1px 1px 1px #ddddd9;
    
}

.stepButton {
    display: flex;
    flex-direction: row;
    width: 5vw;
    align-items: center;
    margin-left: 1rem;
}

.buttonTick {
    width: 6px; 
    height: 6px; 
    border: solid 1px black;
    border-radius: 50%;
}

.buttonTickActive {
    width: 6px; 
    height: 6px; 
    border: solid 1px black;
    border-radius: 50%;
    background-color: #333;
}

.buttonLabel {
    display: inline;
    margin-left: 0.5rem;
}