/* =============================================== */
/*                 FAMILY BUTTON                   */
/* =============================================== */

.familyButton {
    display: flex;
    margin: 1rem;
    width: 15vw;
    height: 40vh;
    background-color: #ebebe7;
    position: relative;
    cursor: pointer;
}

.familyButton:hover {
    background-color: #f5f5f3;
    border: 1px solid #da8600;
}

.familyButtonActive {
    display: flex;
    margin: 1rem;
    width: 15vw;
    height: 40vh;
    position: relative;
    cursor: pointer;
    background-color: #f5f5f3;
    border: 1px solid #da8600;
}

#familyButtonLabel {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: sans-serif;
    font-size: 0.8rem;
}

/* =============================================== */
/*                 CANOPY BUTTON                   */
/* =============================================== */

.canopyButton {
    display: flex;
    margin: 1rem;
    width: 15vw;
    height: 27vh;
    background-color: #ebebe7;
    position: relative;
    cursor: pointer;
}

.canopyButton:hover {
    background-color: #f5f5f3;
    border: 1px solid #da8600;
}

.canopyButtonActive {
    display: flex;
    margin: 1rem;
    width: 15vw;
    height: 27vh;
    position: relative;
    cursor: pointer;
    background-color: #f5f5f3;
    border: 1px solid #da8600;
}

#canopyButtonLabel {
    position: absolute;
    bottom: 0;
    left: 50%;
    right: 50%;
    transform: translate(-50%);
    height: 2rem;
    margin: 0;
    width: fit-content;
    padding-left: 2.2rem;
    padding-right: 2.2rem;
    padding-top: 1rem;
    text-align: center;
    font-family: sans-serif;
    font-size: 0.8rem;
    background-color: #ffffff;
}

