html, body {
  margin: 0;
  padding: 0;
}

.App {
  
}

.header {
  display: block;
  height: 4vh;
  justify-content: center;
  color: #333;
  padding: 0.5rem;
  /* box-shadow: 0px 1px 1px 1px rgb(233, 233, 233);  */
  box-shadow: 0px 1px 1px 1px #ececec;
  
}

.logoLodes {
  display: grid;
  height: 100%;
  margin: auto;
}

.langMenu {
  position: fixed;
  top: 10px;
  right: 10px;
  display: flex;
  height: 4vh;
  align-items: center;
}

#lang {
  display: flex;
  margin: 0;
  padding-right: 0.2rem;
  text-transform: uppercase;
  font-size: 1.8vh;
}

#langLogo {
  height: 2.5vh;
}

#intro {
  padding: 1rem;
  
}

#introTitle {
  font-family: sans-serif;
  font-size: 0.7rem;
  margin: 0;
}

#introNotes {
  font-family: 'Times New Roman', Times, serif;
  font-size: 1.1rem;
  margin: 0;
}


.configContainer {
  height: 120vh;
}


.footer {
  font-size: 0.5rem;
  color: rgb(84, 84, 84);
  padding-left: 2rem;
}

