/* =========================== ROSE MENU ====================== */


.canopyMenuContainer {
    margin: auto;
    width: 60vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
}

.singleCanopyContainer {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
}

/* .singleCanopyContainer>.canopyButton {
    margin: 0;
